@import url('./stylesheets/colors/backgroundColors.css');
@import url('./stylesheets/colors/textColors.css');
@import url('./stylesheets/navbar.css');
@import url('./stylesheets/menu.css');
@import url('./stylesheets/toggle.css');
@import url('./stylesheets/mainWidget.css');
@import url('./stylesheets/insights.css');
@import url('./stylesheets/card.css');
@import url('./stylesheets/footer.css');
@import url('./stylesheets/timeline.css');

img {
  width: 100%;
  box-shadow: 1px 1px 6px black;
}

.embed-page {
  width: 100% !important;
  height: 800px !important;
}

.my-resume {
  height: 443px !important;
}

.d-load-resume {
  width: auto !important;
}

.d-load-resume {
  box-shadow: 1px 1px 6px black !important;
}

.view-resume {
  box-shadow: 1px 1px 6px black !important;
}

.see-resume-btn {
  height: 200px;
  font-size: 40px;
  outline: none !important;
}

#skillsButton {
  width: 45%;
}

#resumeButton {
  width: 45%;
}

.btn:focus,
.btn:active {
  outline: none !important;
  box-shadow: none;
}

.project-img:hover iframe {
  visibility: show;
}

.mepic {
  width: 20%;
}

.education-img {
  width: 20% !important;
}

@media (max-width: 869px) {
  #menuWidget {
    width: 10%;
  }

  #mainWidget {
    width: 86%;
  }

  .card-widget {
    width: 100%;
    margin-top: 20px;
    margin-left: 1px !important;
    margin-right: 1px !important;
  }
}

@media (max-width: 576px) {
  .card-widget {
    width: 100%;
    margin-top: 20px;
    margin-left: 1px !important;
    margin-right: 1px !important;
  }

  .intro-card {
    flex-direction: column !important;
  }

  .intro-para {
    width: 100% !important;
  }

  .mepic {
    width: 100%;
  }

  .resume-and-skills {
    flex-direction: column !important;
  }

  #resumeButton {
    width: 100% !important;
  }

  #skillsButton {
    width: 100% !important;
  }

  .d-load-resume {
    width: 100% !important;
    text-align: center;
  }

  .view-resume {
    text-align: center;
  }

  #menuWidget {
    width: 96%;
  }

  .menu-link {
    justify-content: space-evenly;
  }

  #menuWidget:hover .menu-link {
    justify-content: space-evenly;
  }

  #menuWidget:hover {
    width: 96%;
  }

  .my-menu-link {
    display: contents;
  }

  #mainWidget {
    margin-top: 20px;
    width: 96%;
    margin-right: 2px;
  }

  .mobile-view {
    flex-wrap: wrap;
    flex-direction: column !important;
    justify-content: center;
  }

  #nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .nav-search-links {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .nav-link {
    margin-right: 0px !important;
  }
}
